
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: '',
components: {
Group,
XButton,
XInput,
Toast
},
data () {
return {
toastData: {
isShow: false,
type: 'warn',
text: ''
},
banner: '',
errorImg: 'this.src="' + require('../../assets/images/test/test-banner.jpg') + '"',
code: ''
}
},
created () {
this.axios.post('web/adlist', {
code: 'b3'
}).then(response => {
this.banner = this.base_img + response.data[0].img
}).catch(err => {
console.log(err)
})
},
methods: {
test () {
if (!this.code) {
this.toastData = {
isShow: true,
type: 'warn',
text: '请输入验证码'
}
return false
}
this.axios.get('/web/isfake/' + this.code).then(res => {
let text = ''
let type = 'success'
if (res.success === true) {
if (res.data) {
type = 'success'
text = '经系统验证,您所查询的防伪编码(' + this.code + ')存在.谢谢您的查询!'
} else {
type = 'warn'
text = '经系统验证,您所查询的防伪编码(' + this.code + ')不存在,请核实后再试.谢谢您的查询!'
}
} else {
type = 'warn'
text = res.errMsg
}
this.toastData = {
isShow: true,
type: type,
text: text
}
}).catch(err => {
console.log(err)
})
}
}
}



